   ========================================================================== */
/*
  By default template support only modern format fonts
  Without svg. Only: .woff, .woffs
  if ypu need svg comments lines bellow and uncomments "Fonts helper all format fonts"

  Usage:
    1. Put your font to `src/assets/fonts/FOLDERNAME/FONTNAME`
      FOLLOW: Files Required
      Example: .woff, .woffs formats
    2. Go to fonts.scss
    3. Use mixon
      Example: @include font-face("OpenSans", "../fonts/OpenSans/opensans");

    Required parameters: @family, @family
    Minor parameters: @weight, @style, @pipeline

    Example Base:
      @include font-face("OpenSans", "../fonts/OpenSans/opensans");
    Example Situation:
      @include font-face("OpenSans", "../fonts/OpenSans/opensansItalic", 400, italic);
*/
/*
  Сhoose one of the ways:
    1. handle menthod,
    2. use mixin;
*/
/*
  // Base Helvetica (example):
  @font-face {
    font-family: "Helvetica-Base";
    src: url('/assets/fonts/Helvetica/Base/Helvetica-Base.eot'); // IE9 Compat Modes
    src: url('/assets/fonts/Helvetica/Base/Helvetica-Base.eot?#iefix') format('embedded-opentype'), // IE6-IE8
         url('/assets/fonts/Helvetica/Base/Helvetica-Base.woff') format('woff'), // Pretty Modern Browsers
         url('/assets/fonts/Helvetica/Base/Helvetica-Base.ttf')  format('truetype'), // Safari, Android, iOS
         url('/assets/fonts/Helvetica/Base/Helvetica-Base.svg') format('svg'); // Legacy iOS
  }
*/
@font-face {
  font-family: "SFProDisplay";
  font-weight: 400;
  font-style: normal;
  src: url("/assets/fonts/SFProDisplay-Regular.woff") format("woff"), url("/assets/fonts/SFProDisplay-Regular.woff2") format("woff2"); }

@font-face {
  font-family: "SFProDisplay";
  font-weight: 600;
  font-style: normal;
  src: url("/assets/fonts/SFProDisplay-Semibold.woff") format("woff"), url("/assets/fonts/SFProDisplay-Semibold.woff2") format("woff2"); }

@font-face {
  font-family: "Ubuntu";
  font-weight: 300;
  font-style: normal;
  src: url("/assets/fonts/Ubuntu-Light.woff") format("woff"), url("/assets/fonts/Ubuntu-Light.woff2") format("woff2"); }

@font-face {
  font-family: "Ubuntu";
  font-weight: 400;
  font-style: normal;
  src: url("/assets/fonts/Ubuntu-Regular.woff") format("woff"), url("/assets/fonts/Ubuntu-Regular.woff2") format("woff2"); }

@font-face {
  font-family: "Ubuntu";
  font-weight: 500;
  font-style: normal;
  src: url("/assets/fonts/Ubuntu-Medium.woff") format("woff"), url("/assets/fonts/Ubuntu-Medium.woff2") format("woff2"); }

@font-face {
  font-family: "Ubuntu";
  font-weight: 700;
  font-style: normal;
  src: url("/assets/fonts/Ubuntu-Bold.woff") format("woff"), url("/assets/fonts/Ubuntu-Bold.woff2") format("woff2"); }

html {
  font-size: 18px;
  color: #fff;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit; }

a {
  text-decoration: none;
  color: inherit; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none; }

._container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0px 10px; }
  @media (max-width: 1272px) {
    ._container {
      max-width: 970px; } }
  @media (max-width: 991.98px) {
    ._container {
      max-width: 750px; } }
  @media (max-width: 767.98px) {
    ._container {
      max-width: none;
      padding: 0 10px; } }

.page-section {
  min-height: 100vh;
  background-image: url("../image/content/section-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding: 74px 0px 50px; }

.scroll-btn {
  padding: 12px 0px 40px 0px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center; }

.social__link:hover .social-icon {
  transition: all 0.2s ease;
  fill-opacity: 1; }
